import React from 'react'
import "./logIn.css"
import {useHistory} from 'react-router-dom'



class LogIn extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            first : "",
            last : "",
            data: this.props.data,
        };
    
    }
    
    updateInput(e){
        this.setState({first: e.target.value})
    }
    updateLast(e){
        this.setState({last: e.target.value})
    }
    handleClick(){
        const data = this.state.data
        const username = this.state.first
        const password = this.state.last
        
        let isLogInValid = false
        console.log({data})
        
        
        
    }

    render(){
        return(
            <div class = "content">
             <div class = "firstName">
                 <label>First Name: </label>
                 <input class = "first" value = {this.state.first} type = "text" placeholder = "First Name ..." onChange = {this.updateInput.bind(this)}/>
             </div>
             <div class = "firstName">
                 <label>Last Name: </label>
                 <input class = "last" value = {this.state.last} type = "text" placeholder = "Last Name ..." onChange = {this.updateLast.bind(this)}/>
             </div>
             <button class = "clickButton" onClick = {this.handleClick.bind(this)}>Log In</button>

         </div>
        );
    }
}

export default LogIn


